  --modal-content-border-radius: .3rem;
  --modal-content-xs-box-shadow: 0 .5rem 1rem rgba(0, 0, 0, 0.075);
  --modal-content-inner-border-radius: calc(.3rem - 1px);
  --modal-content-up-box-shadow: 0 .5rem 1rem rgba(0, 0, 0, 0.15);
  --modal-backdrop-background-color: #000;
